.splash {
    display: none;
}

.scroll-down-indicator {
    opacity: 1;
}

h2 {
    transform: none;
    opacity: 1;
}

.fact section p {
    opacity: 1;
}

picture {
    opacity: 1;
    transition: none;
}

.slide-up .picture-container {
    transform: none;
    opacity: 1;
}

.skills section .bar div.width-100 {
    width: 100%;
}
.skills section .bar div.width-90 {
    width: 90%;
}
.skills section .bar div.width-80 {
    width: 80%;
}
.skills section .bar div.width-70 {
    width: 70%;
}
.skills section .bar div.width-60 {
    width: 60%;
}
.skills section .bar div.width-50 {
    width: 50%;
}
.skills section .bar div.width-40 {
    width: 40%;
}
.skills section .bar div.width-30 {
    width: 30%;
}
.skills section .bar div.width-20 {
    width: 20%;
}
.skills section .bar div.width-10 {
    width: 10%;
}
.skills section .bar div.width-0 {
    width: 0;
}

@keyframes slideIn {
    to {
        opacity: 1;
        transform: translate(0, 0);
    }
}

#h-1 {
    opacity: 0;
    transform: translateX(-0.5em);
    animation: slideIn 2500ms cubic-bezier(0.17, 0.67, 0.36, 1) forwards;
}

#h-2 {
    opacity: 0;
    transform: translateX(0.5em);
    animation: slideIn 2500ms 300ms cubic-bezier(0.17, 0.67, 0.36, 1) forwards;
}

#h-tag > div:not(.side) {
    opacity: 0;
    transform: translateY(0.5em);
    animation: slideIn 2500ms 1000ms cubic-bezier(0.17, 0.67, 0.36, 1) forwards;
}

#h-tag > div.side {
    opacity: 0;
    transform: translateY(0.5em);
    animation: slideIn 2500ms 2000ms cubic-bezier(0.17, 0.67, 0.36, 1) forwards;
}
